var that;
export default {
	data() {
		return {
			user: {}
		}
	},

	onLoad() {
		that = this;
		that.getUser();
	},

	methods: {
		getUser() {
			that.$utils.http(that.$api.get_user_info, function(res) {
				that.user = res;
			})
		},

		logout() {
			that.$store.setToken('');
			uni.reLaunch({
				url: '/pages/login'
			})
		},

		tixian() {
			if (that.user.money <= 0) {
				uni.showModal({
					content: '余额不足，无法提现',
					showCancel: false
				});
			} else {
				that.$utils.http(that.$api.withdraw_money, function(res) {
					// uni.navigateTo({
					// 	url:'tixian'
					// })

					uni.navigateTo({
						url: '/pages/user/tixian?money=' + res
					});
				})
			}
		}
	}
}
